































































































import { mapState } from 'vuex';
export default {
	data() {
		return {
			carid: ''
		}
	},
	// 右上角分享事件
	onShareAppMessage(res) {
		let obj = this.cardetail[0];
		return { 
			title: obj.title,
			imageUrl: obj.imgs[0].img
		}
	},
	onLoad(e) {
		console.log(e);
		// 初始化获取车辆id
		this.carid = e.id;
		// 根据id获取车辆信息
		this.$store.dispatch('getCardetail', this.carid);
	},
	methods: {
		makePhoneCall(num) {
			wx.makePhoneCall({
				phoneNumber: num
			})
		}
	},
	computed: {
		...mapState(['cardetail'])
	}
}



